<div id="IntroPage"><router-link to="/work"><div class="introBw"></div></router-link><div class="introContainer" v-motion-slide-visible-top><router-link to="/work"><div class="exitBtn">X</div></router-link><h1>{{pageDatas[idx].title}}</h1><div class="tagContainer"><h3 class="tag" v-for="tag in pageDatas[idx].tags"> {{tag}} | </h3></div><div class="youtube" v-if="pageDatas[idx].video" v-html="pageDatas[idx].video"></div><a id="myhref" v-if="pageDatas[idx].link" :href="pageDatas[idx].link" target="blank">Link</a><p>{{pageDatas[idx].text}}</p><p v-if="pageDatas[idx].Authors">Authors: {{pageDatas[idx].Authors}}</p><div class="htmlContainer" v-if="pageDatas[idx].body" v-html="pageDatas[idx].body"></div><br><img class="images" v-for="img in pageDatas[idx].images" :src="img"></div></div>