<div class="home"><router-view></router-view><div class="cardContainer"><div id="Card" v-for="(card,i) in cards" :style="{'background-image': 'url(' + card.src + ')'}" v-motion-pop-visible><h2 class="CardTitle">{{card.title}}</h2><div class="mask" v-if="card.new_url" @click="changePath(card.new_url)"></div><router-link v-if="card.url" :to="'work'+card.url"><div class="mask" v-bind:class="card.url ? 'pcursor':'pdef'"></div></router-link><div class="maskText" v-if="card.url || card.new_url" style="opacity:1;">{{card.text}} →</div></div></div></div>