puting and Proceedings of the 2021 ACM International Symposium on Wearable Computers.</li><li>Hung, M.-W., Hou, C.-T., <strong>Ho, C.-J.</strong>, Lu, H.-S., Yuan, C. W., Bi, N., Liu, S.-W., Huang, M.-C., & You, C.-W. (2021). Challenges and opportunities in developing systems to assist patients in recovering from gambling addiction. Adjunct Proceedings of the 2021 ACM International Joint Conference on Pervasive and Ubiquitous Computing and Proceedings of the 2021 ACM International Symposium on Wearable Computers.</li><li>Hung, M.-W., Hou, C.-T.,<strong>Ho, C.-J.</strong> , Yuan, C. W., Bi, N., Chen, S.-H., Huang, M.-C., & You, C.-W. (2021). Exploring the opportunities and challenges of enabling clinical-friendly drug psychotherapy with Virtual Reality and Biofeedback Technologies. Extended Abstracts of the 2021 CHI Conference on Human Factors in Computing Systems.</li></ol></div></div><div class="work-experience"><h2>Work Experience</h2><div class="experience-details"><div class="job"><h3>Communications Intern, Taiwan ASML Hsinchu (2022 - 2023)</h3><p>Collaborated with internal divisions and external organizations to advance the company's ESG vision in community engagement through education, culture, and local outreach programs</p><div class="job-detial-container"><img class="job-detial-item project-img" src="https://raw.githubusercontent.com/jerry914/mywebsite/master/img/ASML.jpg" style="width:50%;"></div></div><div class="job"><h3>Full-end Web Developer, Consultation Center of Tsing Hua University (2022)</h3><p>Develop a consultation and case dispatching system to speed up the case dispatching process. The front-end of the system uses the Vue.js framework; the back-end uses Node.js to build a web server.</p><div class="job-detial-container"><p class="job-detial-item"><strong>Honer:</strong> CTCI Scholarship <a href="https://www.ctci.org.tw/8838/talent/ctci-scholarship/44762/44911/" target="_blank">Link</a></p></div></div><div class="job"><h3> <span>Frontend Develo